import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';


function Square(props) {
    return (
        <button className="square" onClick={props.onClick}>
          {props.value}
        </button>
    );
}

  
class Board extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            current:'X',
            waiting: 'O',
            squares: Array(9).fill(null)
        };
    }

    handleClick(i) {
        if (this.state.squares[i] === null) {
            const newsquares = this.state.squares.slice();
            newsquares[i] = this.state.current;
            this.setState({squares:newsquares, current:this.state.waiting, waiting:this.state.current});
            this.checkWinner();
        }
    }

    checkWinner() {
        var count = 0;
        var curr = null;
        for (let i = 0; i < 3; i++) {
            if (this.state.squares[i] !== null) {
                curr = this.state.squares[i];
                count += 1;
                for (let j = i; j < 9; j += 3) {
                    if (this.state.squares[j] === curr) {
                        count += 1;
                    } else {
                        count = 0;
                    }
                }
                if (count === 3) {
                    console.log("player "+curr+" wins!")
                    return;
                }
                count = 0;
            }
        }

        for (let i = 0; i < 9; i +=3) {
            if (this.state.squares[i] !== null) {
                curr = this.state.squares[i];
                count += 1;
                for (let j = i; j < 9; j++) {
                    if (this.state.squares[j] === curr) {
                        count += 1;
                    } else {
                        count = 0;
                    }
                }
                if (count === 3) {
                    console.log("player "+curr+" wins!");
                    return;
                }
                count = 0;
            }
        }

        if (this.state.squares[0] === this.state.squares[4] && this.state.squares[4] === this.state.squares[8] && 
            this.state.squares[0] !== null) {
                console.log("player "+this.state.squares[0]+" wins!");
                return;
        }
        if (this.state.squares[2] === this.state.squares[4] && this.state.squares[4] === this.state.squares[6] && 
            this.state.squares[2] !== null) {
                console.log("player "+this.state.squares[2]+" wins!");
                return;
        }
    }

    renderSquare(i) {
        return <Square value={this.state.squares[i]} onClick={()=>this.handleClick(i)}/>;
    }

    render() {
        const status = 'Next player: '+this.state.current;

        return (
        <div>
            <div className="status">{status}</div>
            <div className="board-row">
            {this.renderSquare(0)}
            {this.renderSquare(1)}
            {this.renderSquare(2)}
            </div>
            <div className="board-row">
            {this.renderSquare(3)}
            {this.renderSquare(4)}
            {this.renderSquare(5)}
            </div>
            <div className="board-row">
            {this.renderSquare(6)}
            {this.renderSquare(7)}
            {this.renderSquare(8)}
            </div>
        </div>
        );
    }
}

class Game extends React.Component {
render() {
    return (
    <div className="game">
        <div className="game-board">
        <Board />
        </div>
        <div className="game-info">
        <div>{/* status */}</div>
        <ol>{/* TODO */}</ol>
        </div>
    </div>
    );
}
}

// ========================================

ReactDOM.render(
<Game />,
document.getElementById('root')
);
